import React, { Component } from "react";

class Tip extends Component {
  constructor(props) {
    super();

    this.state = {
      title: props.title,
      body: props.body,
      icon: props.icon
    };
  }
  render() {
    return (
      <div className="o-tip">
        <img
          src={this.state.icon}
          alt="Ikon"
          className="o-tip-img"
          height="60"
        />
        <div className="o-tip-title">{this.state.title}</div>
        <div className="o-tip-body">{this.state.body}</div>
      </div>
    );
  }
}

export default Tip;
