import React, { Component } from "react";

class Bars extends Component {
  constructor(props) {
    super();

    this.state = {
      you: props.you,
      average: props.average,
      label: props.label,
      show: false
    };
  }
  render() {
    return (
      <div className="o-comparison-bar">
        <div className="o-comparison-bar-label">{this.state.label}</div>
        <div
          className="o-comparison-bars-container"
          onMouseOver={() => this.setState({ show: true })}
          onMouseOut={() => this.setState({ show: false })}
        >
          <div
            className="o-comparison-bar-average"
            style={{ width: this.state.average + "px" }}
          />
          <div
            className="o-comparison-bar-you"
            style={{ width: this.state.you + "px" }}
          />
          <div
            className={
              "o-comparison-tooltip-container" +
              (!this.state.show ? " o-hide" : "")
            }
          >
            <div className="o-comparison-tooltip">
              <div className="o-comparison-tooltip-you">
                {this.state.you} <span className="o-regular">kWh</span>
              </div>
              <div className="o-comparison-tooltip-average">
                {this.state.average} <span className="o-regular">kWh</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Bars;
